@use "global" as *;

.breadcrumb {
  padding-top: myrem(6);
}

.breadcrumb__item {
  display: inline;
  color: $navy;
  font-family: $japanese;
  font-size: myrem(14);
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: myrem(0.5);
}

.breadcrumb__item.breadcrumb__item--white {
  color: $white;
}